<template>
	<div class="task_allRight">
		<div class="task_right" v-for="(item,k) in everyTask" :class="item.class3">
			<div class="task_rightC" >
				<div class="task_rightC-div">{{item.title}}</div>
				<span>{{item.num}}</span>
				<div :class="item.guanzhu">{{item.text}}</div>
				<p>{{item.money}}</p>
				<img :src="item.href" v-show="item.flag" class="img2"/>
				<img :src="item.src" class="img1"/>
			</div>
		</div>
	</div>
</template>
<script>
export default {
  props: {
    everyTask: {
      type: Array,
      default: [
        {
          title: '每天阅读新闻',
          num: '(0/3)',
          money: '+50',
          src: require('../../assets/linkQiandao/链接3_09.png'),
          href: require('../../assets/linkQiandao/链接3_14.png'),
          text: '一键关注',
          guanzhu: 'guanzhu1',
          class3: 'guanzhu2Mg',
          flag: false
        }
      ]
    }
  }
}
</script>

<style scoped>
	.task_right{
		position: relative;
		top:8px;
		margin-bottom:32px;
	}
	.task_rightC-div{
		display: inline-block;
		font-size:15px ;
		color: #000000;
		text-align: left;
	}
	.task_rightC span{
		display: inline-block;
		font-size:15px ;
		color: #acabac;
	}
	.task_rightC p{
		display: inline-block;
		font-size:15px ;
		color: #acabac;
		position: absolute;
		right:17%;
		top:2px;
	}
	.img1{
		position: absolute;
		right:10%;
		top:3px;
		width:18px;
	}
	.guanzhu1{
		display: none;
	}
	.guanzhu2{
		display: inline-block;
		width: 61px;
		height:20px;
		border:1.5px #eeedee solid;
		color:#acabac;
		font-size: 12px;
		text-align: center;
		line-height: 20px;
		margin-left: 0px;
	}
	.guanzhu2Mg{
		position: relative;
		top: 40px;
	}
	.guanzhu3Mg{
		position: relative;
		top: 0px;
	}
	.img2{
		display: inline-block;
		width: 90px;
		float: right;
		margin-top: -30px;
		margin-right:10%;
	}
</style>



