  background: #ffffff;
}

.tesk-enter-active {
  animation: pageLeft .5s;
}
.tesk-leave-active {
  animation: pageLeft .5s reverse;
}
@keyframes pageLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

.outer{
  position: absolute;
  width: 160px;
  height: 160px;
  transform: rotate(125deg);
  clip: rect(0px,100px,200px,0px);
  border-radius: 152.5px;
  border:9.5px solid #cccccc;
